* {
  box-sizing: border-box;
}
body{
  background: white;
  margin: 0;
  font-family: Roboto,sans-serif;
}

.App {
  text-align: center;
  display: flex;
  flex-direction: column;
}

.App-logo {
  height: 250px;
}

h1{
  color: #E0003C;
 padding-right: 10px;
  margin-right: 10px;
}


.App-header {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: left;
  font-size: calc(5px + 2vmin);
  color: white;
  border-bottom: 4px solid #E0003C;
}

.App-body {

height: 400px;
text-align: left;
padding: 40px;
}
.App-footer {
  border-top: 4px solid #E0003C;
  height: 100px;
  font-style: italic;
  margin-bottom: 0%;
  }
